




















import { Vue, Component } from "vue-property-decorator";

import logger from "../src/logger";

@Component({
  components: {}
})
export default class Index extends Vue {
  info = "Hello world!!";
  log = "1111";

  created() {
    logger.add("no start");
    logger.start(content => {
      this.log = content;
    });
    logger.add("start1");
    logger.add("start2");
  }
}



